import React, { useEffect } from 'react'
import { useState } from 'react'
import "../styles/homeScreen.css";
import { Button, Card, Image } from 'react-bootstrap';
import { openInNewTab } from '../utils/OpenInNewTab';
import { SignOutButton } from './SignOutButton';
import { useMsal } from "@azure/msal-react";
import { loginRequest } from "../authConfig";
import { callMsGraph } from "../graph";
import { ProfileData } from './ProfileData';
import TopBar from './TopBar';
import { useIsAuthenticated, AuthenticatedTemplate, UnauthenticatedTemplate } from "@azure/msal-react";
import { Navigate } from "react-router-dom";
import FirstPage from './FirstPage';
import { normalizeEmail } from '../utils/NormalizeEmail';
import { API_END_POINT } from '../config';
import axios from 'axios';
import Loader from './Loader';

var CryptoJS = require("crypto-js");

const HomeScreen = (props) => {
    const { instance, accounts } = useMsal();
    const [graphData, setGraphData] = useState(null);
    const isAuthenticated = useIsAuthenticated();
    let data = JSON.parse(localStorage.getItem('responseObject'));
    const [retrievedData, setRetrievedData] = useState(data);
    const [moduleList, setModuleList] = useState([]);
    const [homeList] = useState([{
        "Module_Id": "98828",
        "Module_Name": "Mystery Shopper",
        "Module_Desc": "Mystery shopper",
        "Is_Active": "TRUE",
        "Redirect_URL": "/msp/",
        "Requires_Tracking": "0",
        "Requires_VPN": "0",
        "Seq": 182,
        "OwnerEmail": "vishal.jani@nielseniq.com",
        "Image_Detail": ""
    },
    {
        "Module_Id": "98836",
        "Module_Name": "VizIT",
        "Module_Desc": "VizIT",
        "Is_Active": "TRUE",
        "Redirect_URL": "/VizIT/",
        "Requires_Tracking": "1",
        "Requires_VPN": "0",
        "Seq": 156,
        "Application_Id": "4",
        "Application_Name": "Analytics",
        "Application_Description": "This is description for Analytics",
        "App_Img_URL": "resources/images/appimgs/Analytics.jpg",
        "Image_Detail": ""
    },
    {
        "Module_Id": "3",
        "Module_Name": "CINTRAL",
        "Module_Desc": "Inventory and tracking for devices & equipment",
        "Is_Active": "TRUE",
        "Redirect_URL": "/Asset_Management/",
        "Requires_Tracking": "1",
        "Requires_VPN": "0",
        "Seq": 3,
        "Application_Id": "2",
        "Application_Name": "Enabling Content",
        "Application_Description": "This is description for Enabling Content",
        "App_Img_URL": "resources/images/appimgs/EnablingContent.jpg",
        "Image_Detail": ""
    }
        ,
    {
        "Module_Id": "98831",
        "Module_Name": "BenchmarX",
        "Module_Desc": "/advpurchases/",
        "Is_Active": "TRUE",
        "Redirect_URL": "/advpurchases/",
        "Requires_Tracking": "1",
        "Requires_VPN": "0",
        "Seq": 151,
        "Image_Detail": ""
    }
        ,
    {
        "Module_Id": "98842",
        "Module_Name": "Digital Shopper",
        "Module_Desc": "Self Collection console for Barter, MyStore and Purchase",
        "Is_Active": "TRUE",
        "Redirect_URL": "/selfcollection",
        "Requires_Tracking": "0",
        "Requires_VPN": "0",
        "Seq": 162,
        "OwnerEmail": "srinivasulu.kopparapo.consultant@nielseniq.com",
        "Image_Detail": ""
    },
    {
        "Module_Id": "5",
        "Module_Name": "CI-Workflow",
        "Module_Desc": "Backoffice, Work Order and  Planning, Scheduling and Route Optimization",
        "Is_Active": "TRUE",
        "Redirect_URL": "/workflowEurope/",
        "Requires_Tracking": "1",
        "Requires_VPN": "0",
        "Seq": 5,
        "Application_Id": "3",
        "Application_Name": "CI-Workflow",
        "Application_Description": "This is description for Workflow",
        "App_Img_URL": "resources/images/appimgs/Workflow.jpg",
        "Image_Detail": ""
    },

    {
        "Module_Id": "98846",
        "Module_Name": "Shopper Walk",
        "Module_Desc": "Digital Collection Tool V2",
        "Is_Active": "TRUE",
        "Redirect_URL": "/dct_v2/",
        "Requires_Tracking": "1",
        "Requires_VPN": "0",
        "Seq": 166,
        "Application_Id": "1",
        "Application_Name": "Data Collection",
        "Application_Description": "This is description for Data Collection",
        "Image_Detail": ""
    },

    ]);
    const [noUserFound, setNoUserFound] = useState(false);
    const [noUserMessage, setNoUserMessage] = useState("");
    const [isLoading, setIsLoading] = useState(true);
    const encryptedKey = ")L1_=&*25!((fh_";

    useEffect(() => {
        try {
            if (localStorage.getItem('responseObject') === undefined || localStorage.getItem('responseObject') === null) {

                if (accounts.length > 0) {
                    instance.acquireTokenSilent({
                        ...loginRequest,
                        account: accounts[0]
                    }).then((response) => {
                        callMsGraph(response.accessToken)
                            .then((response) => {
                                if (response.userPrincipalName) {
                                    response.userPrincipalName = normalizeEmail(response.userPrincipalName.toLowerCase());
                                    setGraphData(response);
                                    if (process.env.NODE_ENV === 'development') {
                                        console.log(response, "inner response");
                                    }
                                    localStorage.setItem('responseObject', JSON.stringify(response));
                                    setRetrievedData(response);
                                }


                            })
                    }).catch(err => {
                        console.error(err, "Error MSCallGraph");

                    })
                }
            }

        } catch (error) {
            console.error(error);
        }



    }, [])

    useEffect(() => {
        if (retrievedData?.userPrincipalName) {
            const postData = { "ContactEmail": retrievedData.userPrincipalName };

            // Make a POST request
            axios.post(`${API_END_POINT.BASE_URL.CI_SERVICE}/ci-portal/landingPageDetails`, postData)
                .then((response) => {
                    // Handle the successful response
                    if (response?.data?.data?.getModules) {
                        setModuleList(response?.data?.data?.getModules);
                    } else {
                        console.log(response?.data, "data response");
                        setNoUserMessage(response?.data?.data);
                        setNoUserFound(true);
                    }
                    setIsLoading(false);
                })
                .catch((error) => {
                    // Handle errors
                    console.error('Error:', error.message);
                    setIsLoading(false);
                });
        }
    }, [retrievedData?.userPrincipalName]);

    const navigate = (module) => {
        var ciphertext = CryptoJS.AES.encrypt(retrievedData?.userPrincipalName, encryptedKey).toString();
        // openInNewTab(`http://${module.Redirect_URL}-dev.azure-conins-np.nielsencsp.net/?k=${ciphertext}&j=${encryptedKey}`);

        // openInNewTab(`http://msp-dev.azure-conins-np.nielsencsp.net/?k=${ciphertext}&j=${encryptedKey}`);
        localStorage.setItem('ContactEmail', retrievedData?.userPrincipalName);

        openInNewTab(module.Redirect_URL);

    }
    return (
        <>
            <AuthenticatedTemplate>
                {isLoading === false ?
                    <>  {retrievedData && retrievedData.userPrincipalName ? (
                        <TopBar userDetails={retrievedData.userPrincipalName} />
                    ) : null}
                        <div className='maincommondiv'>
                            <div className="landingpagemaindiv">
                                {!noUserFound ?
                                    <>
                                        <div className="landingPageHeading">My Modules</div>
                                        {moduleList.map((module, index) => {
                                            return (
                                                <div className='appmodulepanelLanding' style={{ 'marginLeft': '10px', 'cursor': 'pointer' }}
                                                    key={module.Module_Id}
                                                    onClick={() => navigate(module)}>
                                                    {/* <Card style={{ width: '10rem', height: '10rem' }}>
                                            <Card.Img variant="top"
                                                style={{
                                                    'width': '6rem', 'marginLeft': '30px',
                                                    'marginTop': '5px'
                                                }}
                                                src={"data:image/png;base64," + module.Image_Detail}
                                            />
                                            <Card.Body>
                                                <Card.Text className='module_name_label'>
                                                    {module.Module_Name}
                                                </Card.Text>
                                            </Card.Body>
                                        </Card> */}
                                                    <div className="imageDiv">
                                                        <Image
                                                            src={"data:image/png;base64," + module.Image_Detail}
                                                            className="gridpanelImageLanding" />
                                                        <span >
                                                            <div className="module_name_label"> {module.Module_Name}</div></span></div>

                                                </div >
                                            )
                                        })}
                                    </>
                                    :
                                    <div className="landingPageHeading">{noUserMessage}, Please connect with Dev team</div>
                                }


                            </div>

                        </div>

                    </>
                    :
                    <div><Loader /></div>
                }
                <>
                    {/* {            let retrievedObject = JSON.parse(localStorage.getItem('responseObject'));
}           */}
                    {/* <SignOutButton /> */}
                    {/* {retrievedData != null && retrievedData?.userPrincipalName ?
                        <TopBar userDetails={userEmail} />
                        : null} */}



                </>

            </AuthenticatedTemplate>

            {/* <UnauthenticatedTemplate>
                    <FirstPage />
            </UnauthenticatedTemplate> */}
        </>
    )
}

export default HomeScreen